"use client"

import Link from "next/link"
import { useRouter } from "next/navigation" // 추가
import { useState } from "react" // 추가
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Checkbox } from "@/components/ui/checkbox"
import Image from "next/image"

export default function SignupPage() {
  const router = useRouter()
  // 1. 폼 상태 관리를 위한 state 추가
  const [formData, setFormData] = useState({
    name: "",
    email: "", // API 명세의 userName으로 사용
    password: "",
    confirmPassword: ""
  })
  const [isLoading, setIsLoading] = useState(false)

  // AUTH_BASE_URL 설정 (기존 코드 유지)
  const AUTH_BASE_URL = (process.env.NEXT_PUBLIC_AUTH_BASE_URL ?? "http://:12000").replace(/\/$/, "")

  // 2. 회원가입 핸들러 함수 추가
  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault()

    // 유효성 검사
    if (formData.password !== formData.confirmPassword) {
      alert("비밀번호가 일치하지 않습니다.")
      return
    }

    if (formData.password.length < 8) {
      alert("비밀번호는 8자 이상이어야 합니다.")
      return
    }

    setIsLoading(true)

    try {
      const response = await fetch(`${AUTH_BASE_URL}/v1/user/auth/signup`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          userName: formData.email, // 이메일을 userName으로 매핑
          password: formData.password,
          name: formData.name,
          slackId: "" // 선택 사항이므로 빈 값 전송
        }),
      })

      const data = await response.json()

      if (response.ok) {
        alert("회원가입에 성공했습니다! 로그인 페이지로 이동합니다.")
        router.push("/login")
      } else {
        alert(data.message || "회원가입 실패: 입력 정보를 확인해주세요.")
      }
    } catch (error) {
      console.error("회원가입 에러:", error)
      alert("서버 통신 중 오류가 발생했습니다.")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center p-4 py-12">
      <div className="w-full max-w-md space-y-8">
        <div className="text-center space-y-2">
          <Link href="/" className="inline-block">
            <Image src="/logo.png" alt="Next Me" width={150} height={40} className="h-10 w-auto mx-auto" />
          </Link>
          <h1 className="text-2xl font-bold">계정 만들기</h1>
          <p className="text-muted-foreground">미래를 위한 첫 걸음을 시작하세요</p>
        </div>

        <Card className="p-6 space-y-6">
          <div className="space-y-3">
            <Button
              variant="outline"
              className="w-full h-11 bg-white hover:bg-gray-50 text-gray-900 border-gray-300"
              onClick={() => alert("Google 회원가입 기능은 인증 설정 후 활성화됩니다")}
            >
              <svg className="h-5 w-5 mr-2" viewBox="0 0 24 24">
                <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" />
                <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" />
                <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" />
                <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" />
              </svg>
              Google로 회원가입
            </Button>

            <Button
              variant="outline"
              className="w-full h-11 bg-[#FEE500] hover:bg-[#FDD835] text-gray-900 border-[#FEE500]"
              onClick={() => { window.location.href = `${AUTH_BASE_URL}/oauth2/authorization/kakao` }}
            >
              <svg className="h-5 w-5 mr-2" viewBox="0 0 24 24" fill="#000000">
                <path d="M12 3C6.477 3 2 6.477 2 10.75c0 2.76 1.88 5.17 4.67 6.5-.2.74-.77 2.87-.88 3.32-.13.54.2.54.42.39.16-.11 2.55-1.78 3.52-2.45.75.1 1.52.16 2.27.16 5.523 0 10-3.477 10-7.75S17.523 3 12 3z" />
              </svg>
              Kakao로 회원가입
            </Button>

            <Button
              variant="outline"
              className="w-full h-11 bg-[#03C75A] hover:bg-[#02B350] text-white border-[#03C75A]"
              onClick={() => { window.location.href = `${AUTH_BASE_URL}/oauth2/authorization/naver` }}
            >
              <svg className="h-5 w-5 mr-2" viewBox="0 0 24 24" fill="white">
                <path d="M16.273 12.845L7.376 0H0v24h7.726V11.156L16.624 24H24V0h-7.727v12.845z" />
              </svg>
              Naver로 회원가입
            </Button>
          </div>

          <div className="relative">
            <div className="absolute inset-0 flex items-center">
              <div className="w-full border-t border-border"></div>
            </div>
            <div className="relative flex justify-center text-xs uppercase">
              <span className="bg-card px-2 text-muted-foreground">또는</span>
            </div>
          </div>

          {/* Email Signup Form - 3. onSubmit 연결 및 value/onChange 추가 */}
          <form className="space-y-4" onSubmit={handleSignup}>
            <div className="space-y-2">
              <Label htmlFor="name">이름</Label>
              <Input 
                id="name" 
                type="text" 
                placeholder="홍길동" 
                className="bg-background" 
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="email">이메일</Label>
              <Input 
                id="email" 
                type="email" 
                placeholder="example@email.com" 
                className="bg-background" 
                value={formData.email}
                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">비밀번호</Label>
              <Input 
                id="password" 
                type="password" 
                placeholder="8자 이상" 
                className="bg-background" 
                value={formData.password}
                onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="confirm-password">비밀번호 확인</Label>
              <Input 
                id="confirm-password" 
                type="password" 
                placeholder="비밀번호 재입력" 
                className="bg-background" 
                value={formData.confirmPassword}
                onChange={(e) => setFormData({ ...formData, confirmPassword: e.target.value })}
                required
              />
            </div>
            <div className="flex items-start space-x-2">
              <Checkbox id="terms" required />
              <label
                htmlFor="terms"
                className="text-sm text-muted-foreground leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
              >
                <Link href="#" className="text-primary hover:underline">
                  이용약관
                </Link>
                과{" "}
                <Link href="#" className="text-primary hover:underline">
                  개인정보처리방침
                </Link>
                에 동의합니다
              </label>
            </div>
            <Button type="submit" className="w-full" disabled={isLoading}>
              {isLoading ? "처리 중..." : "회원가입"}
            </Button>
          </form>
        </Card>

        <p className="text-center text-sm text-muted-foreground">
          이미 계정이 있으신가요?{" "}
          <Link href="/login" className="text-primary hover:underline font-medium">
            로그인
          </Link>
        </p>
      </div>
    </div>
  )
}